import React, { useEffect, useState } from 'react';
import AceEditor from 'react-ace';

import 'ace-builds/src-noconflict/mode-c_cpp';
import 'ace-builds/src-noconflict/theme-monokai';

const CodeEditor = () => {

    const [questions, setQuestions] = useState([]);
    const [id, setId] = useState(1);
    const [code, setCode] = useState('');
    const [isDisabled, setIsDisabled] = useState(false);
    const [bulbs, setBulbs] = useState([null, null, null, null, null]);

    const dropDownChange = (currentId) => {
        setBulbs([null, null, null, null, null]);
        setId(currentId);
        const placeHolder = questions.find(question => question.id === Number(currentId)).outer_shell;
        setCode(placeHolder.replace(/\\n/g, '\n').replace(/\\r/g, '\r'));
    }

    const handleChange = (newCode) => {
        setCode(newCode);
    }

    useEffect(() => {
        const fetchQuestions = async () => {
            try {
                const response = await fetch('http://localhost:5500/questions', {
                        method: 'GET',
                        headers: { 'Content-Type': 'application/json' }
                    });
                const data = await response.json();
                setQuestions([...data.questions]);
                const firstQ = data.questions[0].outer_shell;
                setCode(firstQ.replace(/\\n/g, '\n').replace(/\\r/g, '\r'));
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        } 
        fetchQuestions();
    }, []);

    const runAPI = () => {
        setIsDisabled(true);
        fetch('http://localhost:5500/run', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ id, code })
        })
            .then(response => response.json())
            .then(data => {setBulbs(data.result); setIsDisabled(false);})
            .catch(error => console.error('Error: ', error));
    }

    return(
        <div className='flex flex-col h-screen w-full items-center justify-center'>
            <h1 className='text-2xl text-blue-900 mb-3'>C++ Editor</h1>
            <select
            value={id}
            onChange={(e) => dropDownChange(e.target.value)}
                className='px-4 py-2 m-2 text-gray-700 border border-gray-400 rounded-md shadow-md'
            >
                {
                    questions.map((question, index) => 
                    <option key={index} value={question.id}>
                        {question.title}
                    </option>)
                }
            </select>
            <div className='w-[35rem] h-[20rem] rounded-lg shadow-md'>
                <AceEditor
                    mode="c_cpp"
                    theme="monokai"
                    onChange={handleChange}
                    value={code}
                    name="code-editor"
                    editorProps={{ $blockScrolling: true }}
                    fontSize={25}
                    width="100%"
                    height="100%"
                />
            </div>
            <div className='flex'>
            <button 
                className={`w-16 h-8 mt-3 ${ isDisabled ? 'bg-yellow-100 cursor-not-allowed' : 'bg-yellow-400 hover:bg-yellow-500' } text-green-900 rounded-lg shadow-md`}
                onClick={runAPI}
                disabled={isDisabled}
            >RUN</button>
            {
                bulbs.map(bulb => {if (bulb === true) {
                        return <div className='w-16 h-8 m-3 bg-green-400 rounded-lg shadow-md'></div>
                    } else if (bulb === false) {
                        return <div className='w-16 h-8 m-3 bg-red-400 rounded-lg shadow-md'></div>
                    } else {
                        return <div className='w-16 h-8 m-3 bg-gray-400 rounded-lg shadow-md'></div>
                    } 
                })
            }
            </div>
        </div>
    );
}

export default CodeEditor;